import React, {useEffect, useState, useRef} from 'react'

import ServiceTile from './service-tile'

import './service-panel.scss'

const services = [
    
        {
            stringCode: 'all',
            title: ' " "',
            description: '     '
        },
        {
            stringCode: 'scenario',
            title: ' ',
            description: ' ,  ,  '
        },
        {
            stringCode: 'film',
            title: '',
            description: '     ,  '
        },
        {
            stringCode: 'edit',
            title: '',
            description: ' ,   '
        },
        {
            stringCode: 'animation',
            title: '',
            description: '2D  3D , -,  whiteboard ,  '
        },
        {
            stringCode: 'dubbing',
            title: ' ',
            description: '    ,   '
        },
        {
            stringCode: 'language',
            title: ' ',
            description: '   ,  '
        },
        {
            stringCode: 'budget',
            title: ' ',
            description: '  ,  ,  '
        }
]

const ServicePanel = ()=>{

    const [show, setShow] = useState(false)
    const ref = useRef(null)

    window.addEventListener('scroll', ()=>{
        if(ref.current && document.documentElement.clientHeight > ref.current.getBoundingClientRect().y){
            setShow(true)
        }
    })

    return(
        <div className={`service-panel ${show? 'show': ''}`} ref={ref}>
            {services.map(service=>{
                return <ServiceTile
                    key={service.stringCode}
                    stringCode={service.stringCode}
                    title={service.title}
                    description={service.description}
                />
            })}
        </div>
    )
}

export default ServicePanel