import {getNewTypes} from '../utils'

const updateLanguageTypes = (state, {type, payload}) =>{
    if (state===undefined){
        return [
            {
                name: '/',
                stringCode: 'rus',
                error: false
            },
            {
                name: '   ',
                stringCode: 'eng',
                error: false
            },
            {
                name: ' ',
                stringCode: 'both',
                error: false
            }
        ]
    }

    return getNewTypes(state, {type, payload}, 'languageTypes')

}

export default updateLanguageTypes