import React, {useRef, useEffect, useState} from 'react'
import {useLocation} from 'react-router-dom'

import {isInTheView} from '../utils'

import './head-block.scss'

const headBlocks = [
    {
        id: '0',
        stringCode: 'examples',
        headline: ' ',
        description: '  -  ,    .    ,       ,    .'
    },
    {
        id: '1',
        stringCode: 'services',
        headline: ' ',
        description: '               .          .'
    },
    {
        id: '2',
        headline: '',
        stringCode: 'contact',
        description: ` !         .   ,     .`
    },
]

const HeadBlock = ({ idx }) => {

    const [appeared, setAppeared] = useState(false)

    const location = useLocation()

    const ref = useRef(null)

    const { description, headline, stringCode } = headBlocks[idx]

    useEffect(()=>{
        if(location.hash===`#${stringCode}`){
            ref.current.scrollIntoView({block: "start", behavior: "smooth"})
        }
    }, [location, ref])

    window.addEventListener('scroll', () => {
        if (isInTheView(ref)) {
            setAppeared(true)
        }
    })

    return (
        <div className={`head-block ${stringCode} ${appeared && 'appeared'}`} ref={ref}>
            <div className={`flex-wrapper`}>
                <div className={`headline`}>
                    <h2>
                        {headline}
                    </h2>
                </div>
                <div className={`description ${stringCode}`}>
                    <p>
                        {description}
                    </p>
                </div>
            </div>

        </div>
    )
}

export default HeadBlock