import {getNewTypes} from '../utils'

const updateWannaTypes = (state, {type, payload}) =>{
    if (state===undefined){
        return [
            {
                name: '',
                stringCode: 'explain',
                error: false
            },
            {
                name: '',
                stringCode: 'learning',
                error: false
            },
            {
                name: '',
                stringCode: 'adVideo',
                error: false
            },
            {
                name: '',
                stringCode: 'presentation',
                error: false
            },
            {
                name: '/',
                stringCode: 'vblog',
                error: false
            },
            {
                name: '',
                stringCode: 'other',
                error: false
            }
        ]
    }

    return getNewTypes(state, {type, payload}, 'wannaTypes')

}

export default updateWannaTypes